import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import { api } from '@/config/common.js'
import { createProductCategory } from '@/api/product.js'
import getGlobalData from '@/utils/business.js'
export default {
	components: {
		uniSearchBar,
		cuPanel,
		cuCell,
		uniList,
		uniListItem,
		uniPopup
	},
	data() {
		return {
			name: '',
			title: '产品分类',
			datas: [],
			searchDatas: [],
			reqData: {
				productcategoryname: '',
				status: 1
			}
		}
	},
	onLoad(options) {
		this.name = options.name
	},
	onShow() {
		this.datas = uni.getStorageSync('productCategory').productCategories
		this.datas = this.datas.filter((item) => {
			return item !== '所有分类'
		})
		this.searchDatas = this.datas
		console.log(this.searchDatas)
	},
	computed: {
		headerHeight() {
			return this.$headerIsSearchHeight
		},
		mainHeight() {
			return this.$mainIsSearchHeight
		}
	},
	methods: {
		handleClickItem(val) {
			let pages = getCurrentPages()
			let prevPage = pages[pages.length - 2]
			prevPage.setData(
				{ 
					rData: { key: 'type', value: val}
				}
			)
			uni.navigateBack({
				delta: 1
			})
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					if (!item) {
						item = ''
					}
					return item.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		},
		handleShowPopup() {
			this.reqData = {
				productcategoryname: '',
				status: 1
			}
			this.$refs.popup.open()
		},
		handleStatusChange(val) {
			if (val.detail.value) {
				this.reqData.status = 1
			} else {
				this.reqData.status = 2
			}
		},
		handleAdd() {
			createProductCategory(api.baseProduct, this.reqData).then(res => {
				this.$refs.popup.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					getGlobalData.getProductCategory().then(res => {
						this.datas = uni.getStorageSync('productCategory').productCategories
						this.datas = this.datas.filter((item) => {
							return item !== '所有分类'
						})
						this.searchDatas = this.datas
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: res.data.returnMessage
					})
				}
			}).catch(error => {
				uni.showToast({
					icon: 'none',
					title: error
				})
			})
		}
	}
}
